.featured {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .featuredItem {
    flex: 1 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  }
  
  .featuredTitle{
      font-size: 20px;
  }
  
  .featuredMoneyContainer{
      margin: 10px 0px;
      display: flex;
      align-items: center;
  }
  
  .featuredMoney{
      font-size: 30px;
      font-weight: 600;
  }
  
  .featuredMoneyRate{
      display: flex;
      align-items: center;
      margin-left: 20px;
  }
  
  .featuredIcon{
      font-size: 14px;
      margin-left: 5px;
      color: green;
  }
  
  .featuredIcon.negative{
      color: red;
  }
  
  .featuredSub{
      font-size: 15px;
      color: gray;
  }
.chart {
    margin: 20px;
    padding: 20px;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  }
  
  .chartTitle {
    margin-bottom: 20px;
  }
  
.widgetSm{
    flex: 1 1;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    padding: 20px;
    margin-right: 20px;
}

.widgetSmTitle{
    font-size: 22px;
    font-weight: 600;
}

.widgetSmImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetSmList{
    margin: 0;
    padding: 0;
    list-style: none;
}

.widgetSmListItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}

.widgetSmUser{
    display: flex;
    flex-direction: column;
}

.widgetSmUsername{
    font-weight: 600;
}

.widgetSmUserTitle{
    font-weight: 300;
}

.widgetSmButton{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}

.widgetSmIcon{
    font-size: 16px !important;
    margin-right: 5px;
}
.widgetLg {
    flex: 1 1;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    padding: 20px;
  }
  
  .widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
  }
  
  .widgetLgTable{
    width: 100%;
    border-spacing: 20px;
  }
  
  .widgetLgTh{
    text-align: left;
  }
  
  .widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
  }
  
  .widgetLgImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
  
  .widgetLgDate,.widgetLgAmount{
    font-weight: 300;
  }
  
  .widgetLgButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
  }
  
  .widgetLgButton.Approved{
      background-color: #e5faf2;
      color: #3bb077;
  }
  .widgetLgButton.Declined{
      background-color: #fff0f1;
      color: #d95087;
  
  }
  .widgetLgButton.Pending{
      background-color: #ebf1fe;
      color: #2a7ade;
  
  }
.admin-container {
  max-width: unset !important;
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 30vh;
  overflow: hidden;
  border-radius: 10px;
}
.SubHeader {
  margin-top: 10px;
}

.d-flex {
  display: flex;
  flex: wrap 1;
}

/*Buttons */
.update {
  font-size: 2em;
  padding-left: 1em;
  padding-right: 1em;
  margin-left: 1em;
  margin-right: 1em;
  background-color: rgb(230, 230, 230);
  color: #6fb327;
  border-radius: 10%;
  box-shadow: 8px 10px 10px 1px rgba(0, 0, 0, 0.5);
}
.update:hover {
  background-color: white;
}
.delete {
  font-size: 2em;
  color: red;
  background-color: rgb(230, 230, 230);
  padding-left: 1em;
  padding-right: 1em;
  border-radius: 10%;
  box-shadow: 8px 10px 10px 1px rgba(0, 0, 0, 0.5);
}
.delete:hover {
  background-color: white;
}
.inline {
  display: inline-block;
}

table {
  border-spacing: 20px;
  -webkit-writing-mode: 100%;
          writing-mode: 100%;
}
table .admin-table-thead {
  padding: 0.75em;
  vertical-align: top;
  text-align: center;
  border: 1px solid grey;
}

table .admin-tableHeader {
  padding: 0.75em;
  vertical-align: top;
  text-align: center;
  background-color: #7ac42b;
  color: #fff;
}

table .admin-tableData {
  vertical-align: center;
  text-align: center;
}
table .admin-tableRow:hover {
  background-color: rgb(233, 233, 233);
}

@media only screen and (max-width: 780px) {
  table thead,
  tbody,
  th,
  td,
  tr {
    display: block;
  }
  .admin-table-thead {
    display: none;
  }

  tr {
    border: 1px solid black;
  }
  td {
    border: none;
    position: relative;
  }
}

/*PRO DASH*/
.requestbtn {
  margin-bottom: 15px;
}

table .pro-table-thead {
  padding: 0.75em;
  vertical-align: top;
  text-align: center;
  border-top: 1px solid black;
}

.table-headings {
  padding: 0.75em;
  vertical-align: top;
  text-align: center;
  background-color: #7ac42b;
  color: #fff;
}
table {
  border-spacing: 20px;
  -webkit-writing-mode: 100%;
          writing-mode: 100%;
}
.noProMessage {
  margin: 15px;
}

table .pro-tableData {
  vertical-align: center;
  text-align: center;
}
.request-sub .requestbtn .requestText {
  font-family: "Nunito", sans-serif;
}

/*PRO REQUEST PAGE*/
.pro-form-group {
  max-width: unset !important;
  position: relative;
  width: 100%;
  border-radius: 15px;
  background-color: #fff;
  min-height: 2rem;
  overflow: hidden;
}

.form-header {
  padding-left: 1rem;
  padding-top: 1rem;
}

.pro-label {
  text-align: right;
  width: 120px;
  padding-right: 2em;
  margin-left: 1.5em;
}

.form-instruction {
  padding-left: 1rem;
}
.request-table {
  display: block;
  margin-left: 1rem;
}

.pro_fields {
  pointer-events: none;
  border: 1px solid grey;
  background-color: lightgrey;
}

